@charset "utf-8";

:root {
    --col__txt_gray: #3e3a39;
}

  
.contents {
    padding: 0 0 80px;

    @media screen and (min-width: 768px) {
        padding-bottom: 120px;
    }
}


.sp {
    display: none;
}

.inner {
    max-width: 1040px;
    padding: 0 20px;
    margin: 0 auto;
  }
  
  .top_wrap {
    // margin-top: 127px;
    background: url(../images/bg_mv.jpg) no-repeat top/cover;
  }

@media screen and (max-width: 767px){
    .sp {
      display: block;
    }
    .pc {
      display: none;
    }  
    
    .top_wrap {
    //   margin-top: 14.93vw;
      background: url(../images/bg_mv_sp.jpg) no-repeat top/cover;
