/*--- list_num_01 ---*/
.list_num_01 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 67px;
    height: 67px;
    background: url(../images/bg_list_num_01.png) no-repeat center/contain;
    line-height: 1;
    font-size: 26px;
    font-weight: 900;
    color: #231815;
  }
  @media screen and (max-width: 767px){
    .list_num_01 {
      width: 55px;
      height: 55px;
      font-size: 19px;
    }  
  }
  
  /*--- list_num_02 ---*/
  .list_num_02 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    height: 46px;
    background: #f5da40;
    border: 1px solid #4d4d4d;
    border-radius: 50px;
    line-height: 1;
    font-size: 24px;
    font-weight: 900;
    color: var(--col__txt_gray);
  }
  @media screen and (max-width: 767px){
    .list_num_02 {
      width: 134px;
      height: 45px;
      font-size: 23px;
    }  
  }
  
  /*--- btn_01 ---*/
  .btn_01__wrap {
    text-align: center;
  }
  .btn_01 {
    display: inline-block;
    min-width: 400px;
    padding: 20px 50px;
    background: #ff6256;
    border-radius: 100px;
    font-size: 26px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    box-shadow: 0 4px 0 #d32920;
    position: relative;
    transition: .2s;
  }
  .btn_01:after {
    content: "";
    display: block;
    width: 18px;
    height: 18px;
    border-top: 5px solid #fff;
    border-right: 5px solid #fff;
    position: absolute;
    top: 51%;
    right: 30px;
    transform: translateY(-50%) rotate(45deg);
  }
  .btn_01:hover {
    opacity: 1;
    transform: translateY(4px);
    box-shadow: 0 0 0 #d32920;
  }
  @media screen and (max-width: 767px){
    .btn_01 {
      width: unset;
      min-width: 335px;
      padding: 16px 45px;
      font-size: 22px;
      box-shadow: 0 3px 0 #d32920;
    }
    .btn_01:after {
      width: 14px;
      height: 14px;
      border-top: 4px solid #fff;
      border-right: 4px solid #fff;
    }  
  }
  
/*---------------------------------
MV
---------------------------------*/
#mv {
    padding: 85px 0 55px;
}

#mv .inner {
    max-width: 810px;
}

#mv .mv__container {
    padding-left: 37.5%;
    text-align: center;
    position: relative;
}

#mv .mv__ttl__top {
    font-size: 18px;
    font-weight: bold;
    color: #225dab;
    line-height: 1.75;
}

#mv .mv__ttl__bottom {
    max-width: 402px;
    margin: 10px auto 5px;
}

#mv .mv__container_child .mv__img_wrap {
    position: absolute;
    top: calc(50% + 20px);
    left: 0;
    transform: translateY(-50%);
}

#mv .mv__container_child .mv__txt {
    color: #225dab;
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 15px;
}

#mv .mv__container_child .mv__txt .txt__s {
    font-size: 18px;
}

#mv .mv__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 24px 5%;
}

#mv .mv__list_item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    min-width: 30%;
    max-width: 40%;
    aspect-ratio: 1 / 1;
    background: url(../images/bg_mv_list_item.png) no-repeat center/contain;
    color: #225dab;
    font-size: 22px;
    font-weight: bold;
    line-height: 1;
    font-feature-settings: "palt";
    padding: 10px;
}

#mv .mv__list_item:nth-child(1) {
    line-height: 1.25;
}

#mv .mv__list_item .txt__s {
    font-size: 17px;
}

#mv .mv__list_item .txt__l {
    font-size: 34px;
}

#mv .btn_01__wrap {
    margin-top: 22px;
}

@media screen and (max-width: 767px) {
    #mv {
        padding: 25px 0 36px;
    }

    #mv .inner {
        max-width: 810px;
    }

    #mv .mv__container {
        padding-left: 0;
    }

    #mv .mv__ttl__top {
        font-size: 3.2vw;
    }

    #mv .mv__ttl__bottom {
        max-width: 337px;
    }

    #mv .mv__container_child {
        display: flex;
    }

    #mv .mv__container_child .mv__img_wrap {
        position: static;
        transform: none;
        width: 32.4%;
    }

    #mv .mv__container_child .mv__txt_wrap {
        width: 67.6%;
    }

    #mv .mv__container_child .mv__txt {
        font-size: 8.25vw;
        text-indent: -1.3em;
        font-feature-settings: "palt";
        text-align: left;
        margin: 2vw 0 10px 1em;
        line-height: 1.3;
    }

    #mv .mv__container_child .mv__txt .txt__s {
        font-size: 5.13vw;
    }

    #mv .mv__list {
        gap: 3.5vw 5%;
    }

    #mv .mv__list_item {
        font-size: 2.7vw;
        padding: 5px;
    }

    #mv .mv__list_item .txt__s {
        font-size: 2.18vw;
    }

    #mv .mv__list_item .txt__l {
        font-size: 4.4vw;
    }

    #mv .btn_01__wrap {
        margin-top: 5px;
    }
}


